<template>
  <div>
    <el-row>
      <el-col>
             <el-select @change="openCollege" v-model="selectCollege"  placeholder="">
                <el-option v-for="college in storeState.colleges" :key="college" :label="college" :value="college"></el-option>
              </el-select>
  <!-- <router-link :to="{name:'college'}"> <el-button @click="truncateFiles"></el-button>  </router-link> -->
         <el-button @click="truncateFiles"></el-button>
      </el-col>
    </el-row>
    <!--  -->
    <el-row :gutter="40">
      <!-- 1  -->
      <el-col :span="10"  v-if="false">
        <el-card>
          <div slot="header">
            <span></span>

            <!--  -->
            <el-date-picker
              style="float:right;"
              @change="changeYear"
              v-model="year"
              type="year"
              placeholder=""
            ></el-date-picker>
            <div style="float:right;"></div>
          </div>

          <div>,26</div>
          <!--  -->
          <el-table :data="files" :row-class-name="fileStateClassName">
            <!--  -->
            <el-table-column label="" prop="college"></el-table-column>
            <el-table-column label="" prop="year"></el-table-column>
            <el-table-column :formatter="formatGrade" label="" prop="grade"></el-table-column>
            <el-table-column :formatter="formatTerm" label="" prop="term"></el-table-column>
            <el-table-column label="" prop="count"></el-table-column>
            <el-table-column :formatter="formatState" label="" prop="state"></el-table-column>
            <!--  -->
            <el-table-column label="">
              <!-- scope.row -->
              <template slot-scope="scope">
                <el-upload
                  :on-success="uploadOk"
                  :on-error="uploadNotOk"
                  action="/api/data/upload"
                  :data="getUploadPostData(scope.row)"
                  :show-file-list="false"
                >
                  <el-button type="text"></el-button>
                </el-upload>
                <el-button
                  v-if="scope.row.state==1"
                  type="text"
                  @click="parseFile(scope.row)"
                ></el-button>
                <el-button type="text" @click="deleteFile(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>

<!-- 2 -->
      <el-col :span="14" :xs="20">      
        <el-card>
          <div slot="header">
            <span></span>
            <div style="float:right">
              <div style="display:inline;padding-right:20px;" >:{{storeState.login.username}}</div>
              <el-button type="success" @click="qiandao"></el-button>
              <!--  -->
              <el-button type="warning" @click="dialogVisible = true"></el-button>
              <el-dialog title=""
                        :visible.sync="dialogVisible"
                        width="30%"
                        :before-close="handleClose">
                          <el-form size="medium">
                        <el-form-item  label="" prop="password1">
                    <el-input placeholder="" v-model="reg.password1" show-password> </el-input>
                    </el-form-item>
                    <el-form-item  label="" prop="password2">
                    <el-input placeholder="" v-model="reg.password2" show-password> </el-input>
                </el-form-item>
                <el-form-item   label="" prop="password3">
                    <el-input placeholder="" v-model="reg.password3" show-password> </el-input>
                </el-form-item>
                          </el-form>

                        <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogVisible = false"> </el-button>
                        <el-button type="primary" @click="changgg"> </el-button>
                        </span>
                        </el-dialog>
              <el-button type="danger" @click="logout"></el-button>
               </div>
            
            
          </div>
          <span></span>

          <el-row>
                <el-col :span="4">
              <el-select v-model="upload.college" placeholder="">
                <el-option v-for="college in storeState.colleges" :key="college" :label="college" :value="college"></el-option>
              </el-select>
            </el-col>
            <!--  -->
            <el-col :span="4">
              
              <el-date-picker v-model="upload.year" type="year" placeholder=""></el-date-picker>
            </el-col>
            <el-col :span="4">
              <el-select v-model="upload.grade" type="year" placeholder="">
                <el-option
                  :label="year.label"
                  :value="year.value"
                  v-for="year in uploadYears"
                  :key="year.value"
                ></el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="upload.term">
                <el-option label="" value="term1"></el-option>
                <el-option label="" value="term2"></el-option>
              </el-select>
            </el-col>
            <el-col :span="12">
              <el-upload
                :limit="1"
                :multiple="false"
                ref="uploadNew"
                :auto-upload="false"
                :data="uploadData"
                action="/api/data/upload"
                :on-success="uploadOk"
                :on-error="uploadNotOk"
                :on-change="uploadChange"
                
              >
                <el-button slot="trigger">Excel</el-button>
                <el-button @click="submitUploadNew"></el-button>
              </el-upload>
            </el-col>
          </el-row>

          <el-table :data="uploaded" :row-class-name="fileStateClassName">
            <el-table-column label="" prop="college"></el-table-column>
            <el-table-column label="" prop="year"></el-table-column>
            <el-table-column :formatter="formatGrade" label="" prop="grade"></el-table-column>
            <el-table-column  :formatter="formatTerm" label="" prop="term"></el-table-column>
            <el-table-column  :formatter="formatState" label="" prop="state"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button @click="deleteFile(scope.row)"></el-button>
                <el-button v-if="scope.row.state==1" @click="parseFile(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>

    


      </el-col>
    </el-row>
  </div>
</template>

<script>
import {
  apiParse,
  apiTruncate,
  apiFiles,
  apiDelete,
  apiUploaded
} from "../api/dataApi.js";

import { Loading } from "element-ui";
import { arrayFill ,errorDialog } from "../utils/tools.js";
import {apiLogout,apiUserInfo,apiUserchange} from '../api/api.js'
import {store} from '../store.js'
import { college } from '../api/testdata';

export default {
  // ,
  name: "datamanage",


  data() {
    return {
      selectCollege:"",
      
      reg:{
         password1:"",
         password2:"",
         password3:"",
      },
      dialogVisible: false,
      storeState:store.state,
      // 2 
      upload: {
        // Date
        year: "",
        grade: "",
        term: "",
        college:""
      },
      // ,uploaduploadData
      uploadData: {},
      // 

      uploaded: [],
      // 1
      // 2019
      year: new Date(2019, 11, 11),
      // 
      files: [
        {
          college: "",
          year: "2018"
        }
      ]
      
    };
  },

  // created2019
  created() {
           console.log("...")
    // 
    apiUserInfo().then(res=>{
      console.log("...")
      console.log(res)
    //  
    console.log(":"+res.data.username)
    store.setUserName(res.data.username);
    }).catch(err=>{
      console.log(":")
      console.log(err)
    })
    // 
    this.switchYear(2019);
    this.requestUpload();
 
  },
  computed: {
    // ,2017 171615
    uploadYears() {
      if (this.upload.year === "") return [];
      console.log(this.upload.year + typeof this.upload.year);
      let year = this.upload.year.getFullYear();
      let result = [];
      for (let j = year; j > year - 3; j -= 1) {
        result.push({
          label: j + "",
          value: j + ""
        });
      }

      return result;
    }
  },
  methods: {
    openCollege(value)
    {
      console.log(":",value)
      this.$router.push({
        name:"college",
        params:{
          collegeid:value
        }
        

      })
    },
    qiandao(){
      this.$message({
        message:",+10",
        type:"warning"
      })
    },
    
    changgg(){
      if(this.reg.password2!=this.reg.password3){
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
        return
      }
      this.dialogVisible=false
      console.log("")
      apiUserchange({
          username:this.storeState.login.username,
          old_password:this.reg.password1,
          password:this.reg.password2,
          password2:this.reg.password3,
      }).then(res=>{
        
        console.log("..")
        console.log(res)
        if(res.code==0)
        {
        this.$message({
          showClose: true,
          message: ',',
          type: 'success'
        });
        this.$router.push({
          name:'login'
        })
        }
        else
        this.$message({
          showClose: true,
          message: ':'+res.msg,
          type: 'error'
        });
      }).catch(err=>{
        console.log(".")
      })
    },
      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    ,
    logout(){
      apiLogout().then(res=>{
        if(res.code==0){
        this.$message({
          message:"!"
        })
        this.$router.push({
          name:'login'

        })
        }
        else{
            errorDialog(this,res.msg)
        }
      }).catch(err=>{})

    },
    // 1 
    submitUploadNew() {
      console.log("...");
      if(this.upload.year==""){
        this.$message("!!")
        return
      }
      // 
      this.uploadData["year"] = this.upload.year.getFullYear();
      this.uploadData["grade"] = this.upload.grade;
      this.uploadData["term"] = this.upload.term;
      this.uploadData["college"] = this.upload.college;
      this.$refs.uploadNew.submit();
    },
    // 
    requestUpload() {
      apiUploaded()
        .then(res => {
          console.log(":");
          console.log(res);
          if(res.code==0)
          arrayFill(this.uploaded, res.data);
          else
          this.$alert(res.data,"",{
            confirmButtonText:""
          })
        })
        .catch(err => {
          console.log("");
        });
    },
    // 2017 17
    formatGrade(row, column, cellValue, index) {
      if(!cellValue)return "null"
      if (cellValue.length == 4) return cellValue.substring(2, 4) + "";
      else return cellValue;
    },
    // 
    formatTerm(row,column,cellValue,index){
      if(cellValue=="term1")return ""
      else if(cellValue=="term2")return ""
      else
      return cellValue
    },
    // ,
    formatState(row,column,cellValue,index){
        if(cellValue==0)
        return ""
        else if(cellValue==1)
        return ""
        else if(cellValue==2)
        return ""
    },
    // 
    truncateFiles() {
      apiTruncate()
        .then(res => {
          console.log("..");
          this.switchYear(this.year.getFullYear());
        })
        .catch(err => {
          console.log("..");
        });
    },
    // 
    deleteFile(param) {
      let postparam = {
        // 
        year: param.year,
        // 
        grade: param.grade,
        // 
        term: param.term,
        college:param.college
      };
       Loading.service({ text: "....", fullscreen: true });
      apiDelete(postparam)
        .then(res => {
              Loading.service({ fullscreen: true }).close();
          console.log("ffff")
          console.log(res)
         
          if(res.code==0){
          this.$message({ type: "success", message: ":"+res.msg });
          // 
          this.switchYear(this.year.getFullYear());
          }
          else
          {
            this.$alert(res.msg, "", {
            confirmButtonText: ""
          });
          }
        })
        .catch(err => {
              Loading.service({ fullscreen: true }).close();
          console.log(err)
          this.$alert(err.data, ",", {
            confirmButtonText: ""
          });
        });
    },
    // ,post
    parseFile(param) {
      console.log(":");
      console.log(param);
      // 
      Loading.service({ text: "....", fullscreen: true });
      let postparam = {
        // 
        year: param.year,
        // 
        grade: param.grade,
        // 
        term: param.term,
        college:param.college
      };
      apiParse(postparam)
        .then(res => {
          // 
          Loading.service({ fullscreen: true }).close();
          console.log(res);
          // 200,
          if (res.code != 0) {
            this.$alert(res.msg, "", {
              confirmButtonText: ""
            });
          } else {
            this.$message({ type: "success", message: "" });
          }
          this.switchYear(this.year.getFullYear());
        })
        .catch(err => {
            this.$alert( ":"+err,"", {
              confirmButtonText: ""
            });
          Loading.service({ fullscreen: true }).close();
        });
    },
    
    // 
    //
    uploadOk(response, file, fileList) {
      if (response.code != 0) {
        this.$alert(response.msg, "", {
          confirmButtonText: ""
        });
      } else {
        this.$message({
          type: "success",
          message: ""
        });
      }
      this.switchYear(this.year.getFullYear());
      this.$refs.uploadNew.clearFiles();
    },
    // 
    uploadNotOk(err, file, fileList) {
      console.log("...");
      console.log(err);
      this.$alert(err, "", {
        confirmButtonText: ""
      });
      this.$refs.uploadNew.clearFiles();
    },
    // 
    uploadChange(file,fileList){
      console.log(":")
      console.log(file)
      console.log(fileList)
      // 1fileList0
      if(fileList.length==1)
      {
        let name = fileList[0].name
        console.log(":"+name)
        // 
        let grade_pattern =/(\d+)/g
        let term_pattern= /(.)/g
        let year_pattern=/(\d{4})-\d{4}/g
        let grade=grade_pattern.exec(name)
        let term = term_pattern.exec(name)
        let year = year_pattern.exec(name)
        console.log(":"+grade)
        console.log(term)
        console.log(year)
        // 
        if(grade.length==2&&term.length==2&&year.length==2)
        {
          term = term[1]
          if(term=="")term="term1"
          else if(term=="")term="term2"
          grade=grade[1]
          year = year[1]
          this.$message({
            type:"success",
            message:`,:${year} :${grade} :${term}`
          })
          this.upload.grade=grade
          this.upload.term = term
          this.upload.year = new Date(year,1,1)
        }
        else
        {
          this.$message({
            type:"error",
            message:""
          })
        }

      }
    },
    // 1-------------------
    // postel-upload
    getUploadPostData(row) {
      return {
        year: row.year,
        grade: row.grade,
        term: row.term
      };
    },
    // 
    fileStateClassName(row, rowIndex) {
      if (row.row.state == 0) return "file-no-upload";
      else if (row.row.state == 1) {
        return "file-ok";
      }
    },
    // ,
    changeYear(date) {
      this.switchYear(date.getFullYear());
    },
    // 0 1
    switchYear(year) {
      this.requestUpload();
      // apiFiles({
      //   year: year
      // })
      //   .then(res => {
      //     console.log("...");
      //     // 
      //     this.files.splice(0, this.files.length);
      //     // 
      //     res.forEach(element => {
      //       this.files.push(element);
      //     });
      //   })
      //   .catch(err => {
      //     this.$alert(err, "", {
      //       confirmButtonText: "ok"
      //     });
      //     console.log("");
      //   });
      console.log(":" + year);
      //
    },
    // 1 
    submitUpload() {
      this.$refs.upload.submit();
    }
    // success() {
    //   console.log("!");
    // },
    // error() {
    //   this.$message("...");
    // }
  }
};
</script>


<style >
/*  */
.el-table .file-no-upload {
  background: oldlace;
}
.el-table .file-ok {
  background: #f0f9eb;
}
</style>