<template>

    <div id=login>
        <el-row type="flex" justify="center">
            <el-col   :span="5" :xs="20" style="margin-top: 200px" >
            <el-form :model="login" :rules="rules" ref="loginForm" size="medium">
                <el-form-item :error="errors.username" label="" prop="username" >
                    <el-input v-model="login.username" ></el-input>
                </el-form-item>

                <el-form-item :error="errors.password" label="" prop="password">
                    <el-input v-model="login.password"  show-password></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button  v-on:click="submitLogin" size="medium"></el-button>
                </el-form-item>
                <a  href="#" @click="register()">?</a>
            </el-form>
       
        </el-col>
        </el-row>
    </div>
    
</template>

<script>

import {apiLogin,apiYears} from '../api/api.js'

//event-bus
import {EventBus} from '../event-bus.js'
import { Loading } from "element-ui";
export default {
    
    name:"login",
    mounted(){
     

    },
    beforeRouteUpdate(){
  
    },
    created(){
        console.log("create....")
        EventBus.$on("showLogin",()=>{
            this.isShow=true
        })
        console.log("")
      EventBus.$emit('hideLogo',false);  
    },//logo
    data(){
        return {
            //
            errors:{
                username:"",
                password:""
            },
        // 
        rules:{
            username:[
                {required:true,message:"", trigger: 'blur'}
            ],
            password:[
                {required:true,message:"", trigger: 'blur'},
                {min:8,max:16,message:"816", trigger: 'blur'   }
            ]
        },

        isShow:true,
        //el-tablemodel
        login:{
        //  username:"jojo",   
        //  password:"jojojojo"
        }

        }
    },
    methods:{
        register(){
            this.$router.replace({name:'register'})
        },
        
        startLogin(){
            Loading.service({ text: "....", fullscreen: true });
             apiLogin({
                username:this.login.username,
                password:this.login.password
            }).then(res=>{
                Loading.service({ fullscreen: true }).close();
                console.log("")
                console.log(res)
                this.isShow=false
                // 
                //this.$router.push("college")
                if(res.code==0)
                {
                    this.$message({
                    type:"success",
                    message:""
                })
               this.$router.push({name:"data"})
                }
                else{
                    console.log("0")
                    let data=res.data;
                    console.log(data)
                        if(data.username !== undefined)
               {
                     let temp = data.username.join(",");
                    if(temp.indexOf('Invalid username')!=-1)
                    this.errors.username=""
               }
                 if(data.password !== undefined)
                  {
                     //
                    let temp = data.password.join(",");
                    console.log(temp)
                    if(temp.indexOf("Incorrect password!")!=-1)
                        this.errors.password="!"
                 }
                }
             
               
                //cookes
            }).catch(err=>{
                Loading.service({ fullscreen: true }).close();
                console.log("")
                console.log(err)
                 let data = err.response.data
                 if(err.response.status==403)
                this.$message({
                    type:"error",
                    message:","
                })
                else{
                  this.$message({
                    type:"error",
                    message:","
                })  
                }


            })
        },
        //
        openLogin(){
            this.isShow=true
        },
        submitLogin(){
            console.log("....")
            this.$refs['loginForm'].validate((valid)=>{
                if(valid)
                {
                    //
                    this.startLogin()
                }
                else
                {
                    //
                }
            })
           




        }
    }

}
</script>

<style>
/*  */
html,body{cursor:url('../images/mouse.png'),auto}
/*  */
#login{
    background: url("../images/ba8bd4e0f53dbde0eedb58c571a98202.jpg");
    background-size: 100% 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%
}
</style>