import Vue from 'vue'
// 
// a1a2
export function arrayFill(a1,a2)
{
    a1.splice(0,a1.length)
    for(let i =0;i<a2.length;i+=1)
    {
        a1.push(a2[i])
    }

}

// 
export function errorDialog(vue,message)
{
    vue.$alert(message,"",{
        confirmButtonText:""
      })
}