<template>
  <div id="bg">
    <el-row type="flex" justify="center" style="margin-right:100px; margin-top: 110px">
      <el-col :span="5" :xs="20">
        <div style="font-size:44px;margin-bottom: 20px;"></div>
        <div style="font-size:28px;margin-bottom: 64px;">
          ,
          <div style="color:#359eff;float:right;"></div>
        </div>

        <el-form size="medium">
          <el-form-item :error="errors.username" label="" prop="username">
            <el-input placeholder="" v-model="reg.username"></el-input>
          </el-form-item>
          <el-form-item :error="errors.password" label="" prop="password">
            <el-input placeholder="" v-model="reg.password" show-password></el-input>
          </el-form-item>
          <el-form-item :error="errors.password2" label="" prop="password2">
            <el-input placeholder="" v-model="reg.password2" show-password></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submit()" size="medium"></el-button>
          </el-form-item>
          <el-form-item>
            <el-tooltip placement="top">
              <div slot="content"></div>
              <div>
                <el-checkbox v-model="isAgree" @change="agree"></el-checkbox>
                <img onclick src="/favicon.ico" />
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { apiRegister } from "../api/api.js";
import { EventBus } from "../event-bus.js";
import { type } from "os";
import { Loading } from "element-ui";
export default {
  name: "bg",
  created() {
    EventBus.$emit("hideLogo", false); //logo
  },
  // 
  methods: {
    agree() {},
    submit() {
      if (!this.isAgree) {
        this.$message({
          message: "",
          type: "warning"
        });
        return;
      }
      if (this.reg.password != this.reg.password2) {
        this.errors.password2 = "";
        return;
      }
    Loading.service({ text: "....", fullscreen: true });
      apiRegister({
        username: this.reg.username,
        password: this.reg.password
      })
        .then(res => {
            Loading.service({ fullscreen: true }).close();
          console.log(":");
          console.log(res);
          if (res.code == 0) {
            this.$message({
              showClose: true,
              message: "...",
              type: "success"
            });
            this.$router.replace({
              name: "login"
            });
          } else {
            let data = res.data;
            if (data.username !== undefined) {
              let temp = data.username.join(",");
              if (temp.indexOf("already exists!") != -1)
                this.errors.username = "";
            }
            if (data.password !== undefined) {
              //
              let temp = data.password.join(",");
              if (temp == "Field must be between 8 and 16 characters long.")
                this.errors.password = "816";
            }
          }
        })
        .catch(err => {
            Loading.service({ fullscreen: true }).close();
          console.log(err);
     
          console.log(":");
          this.$message({
            type: "error",
            message: ""
          });
        });
    }
  },
  data() {
    return {
      isAgree: false,
      //
      errors: {
        username: "",
        password: "",
        password2: ""
      },

      reg: {
        username: "",
        password: "",
        password2: ""
      }
    };
  }
};
</script>

<style>
/*  */
#bg {
  background: url("../images/44ce51cbfee3ac0249cd771f2ad9a7c2.jpg");
  background-size: cover;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}
</style>