<template>
  <div>
    <!--  -->
    <el-row :gutter="40">
      <!--  -->
      <el-col :span="12">
        <el-card  body-style="padding:34px">
          <div slot="header" class="clearfix">
            <span style="font-size:25px"></span>
          </div>
          <div style="font-size:20px">:{{data.studentCard1.term1_avlscore}}</div>
          <div style="font-size:20px">:{{data.studentCard1.term1_yranking}}</div>
          <div style="font-size:20px">:{{data.studentCard1.term1_cranking}}</div>
        </el-card>
         <el-card  body-style="padding:34px">
          <div slot="header" class="clearfix">
            <span style="font-size:25px"></span>
          </div>
          <div style="font-size:20px">:{{data.studentCard2.term2_avlscore}}</div>
          <div style="font-size:20px">:{{data.studentCard2.term2_yranking}}</div>
          <div style="font-size:20px">:{{data.studentCard2.term2_cranking}}</div>
        </el-card>
      </el-col>
      <!--  -->
      <el-col :span="12" :xs="24">
        <el-card>
        <v-chart class="chart" ref="scuhindexscore" autoresize>
           <!-- ref -->
        </v-chart>
        </el-card>
      </el-col>
    </el-row>
    
    <!--  -->
    <el-row :gutter="40" style="margin-top: 25px;">
      <!--  -->
      <el-col :span="12" :xs="24">
        <el-card >
        <div style="font-size:20px"></div>
        <el-table :data="CollegeData"  fit  align="center">
          <el-table-column prop="Collegindex" label="" width="180"></el-table-column>
          <el-table-column prop="Collegscores" label="" width="180"></el-table-column>
          <el-table-column prop="Collegranking" label="" width="180"></el-table-column>
        </el-table>
        </el-card>
      </el-col>
      <!--  -->
      <el-col :span="12" :xs="24">
        <el-card >
         <div style="font-size:20px"></div>
        <el-table :data="ClassData"  fit align="center">
          <el-table-column prop="Classindex" label="" width="180"></el-table-column>
          <el-table-column prop="Classscores" label="" width="180"></el-table-column>
          <el-table-column prop="Classranking" label="" width="180"></el-table-column>
        </el-table>
        </el-card>
      </el-col>
    </el-row>
    <!--  -->
    <el-row>
      <!--  -->
      <el-col>
        <el-collapse v-model="activeNames" @change="handleChange">
          <el-collapse-item title="" name="1">
            <div>{{proposal}}</div>
            <div>{{proposal2}}</div>
          </el-collapse-item>
        </el-collapse>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { StudentData } from "../api/teststudent.js";
import { store } from "../store.js";
import { EventBus } from "../event-bus.js";

export default {
  name: "student",
  mounted() {
    EventBus.$on("studentDataLoad", data => {
      console.log("");
      console.log(data);
      this.data = data;
       //
      this.set_suchindexscore();
      this.hideLoad();

      let score = 0;
      let indexs=['','','','','','','']//
      //
      let minIndex=0
      let temp=this.data.suchindexscores[2].value;
      for (let i=0;i<=temp.length-2;i+=1){
        if (temp[i]<temp[minIndex]){minIndex=i}
      }
      this.proposal2=""+indexs[minIndex]+""

      if (store.state.term === "term1")
        score = this.data.studentCard1.term1_avlscore;
      else score = this.data.studentCard2.term2_avlscore;
      if (score >= 0 && score <= 15) {
        this.proposal = "";
      } else if (score > 15 && score <= 30) {
        this.proposal = "";
      } else if (score > 30 && score <= 50) {
        this.proposal = "";
      } else if (score > 50 && score <= 100) {
        this.proposal = "";
      }
    });
    this.showLoad();
    //
    EventBus.$emit("requestData", "");
  },

  methods: {
    handleChange(val) {
      console.log(val);
    }, //
    showLoad() {
      Object.keys(this.$refs).forEach(key => {
        this.$refs[key].showLoading();
      });
    },
    hideLoad() {
      //
      Object.keys(this.$refs).forEach(key => {
        this.$refs[key].hideLoading();
      });
    },
    set_suchindexscore() {
      console.log("");
      //
      let suchindexscores = this.data['suchindexscores']
      let maxvalue = -1;    
      for(let i =0;i<suchindexscores.length;i+=1)
      {
        for(let j =0;j<suchindexscores[i].value.length;j+=1)
        {
          if(suchindexscores[i].value[j]>maxvalue)
          maxvalue=suchindexscores[i].value[j]
        }
      }

      let option={
        title: { text:`${this.stateStore.termName()} ` },
        tooltip:{},
         legend:{data:['','',''],right:5},
        radar:{
          shape :'circle',
          axisLine :{
            
          },
          indicator:[
                   { name: "", max: maxvalue , color:"black"},
            { name: "", max: maxvalue ,color:"red"},
            { name: "", max: maxvalue ,color:"orange"},
            { name: "", max: maxvalue ,color:"pink"},
            { name: "", max: maxvalue ,color:"green"},
            { name: "", max: maxvalue ,color:"blue"},
            { name: "", max: maxvalue ,color:"purple"}
          ]
        },
        series:[
          { name:'vsvs',
            type:"radar",
            data:suchindexscores
          }

        ]

      }
     
      console.log(option);
      this.$refs["scuhindexscore"].mergeOptions(option);//
    },

    set_CollegeData() {
      
      this.CollegeData.splice(0, this.CollegeData.length);
      this.data["data1"]["CollegeData"].forEach(element => {
        this.CollegeData.push(element);
      });
    },//
    set_ClassData() {
      
      this.ClassData.splice(0, this.ClassData.length);
      this.data["data2"]["ClassData"].forEach(element => {
        this.ClassData.push(element);
      });
    }//
  },

  computed: {
    CollegeData() {
      return this.data.data1.CollegeData;
    },
    ClassData() {
      return this.data.data2.ClassData;
    }
  },
  data() {
    return {
      proposal: [],
      proposal2:[],
      stateStore: store.state,
      data: StudentData,

     
      activeNames: ["1"]//
    };
  }
};
</script>

