<template>
  <div class="logo" v-loading.fullscreen="loading">
    <el-row>
      <el-col :span="12" :xs="24">
        
          <img @click="home" style="width: 400px; " src="/logo.jpg" />
        <div v-if="showSelect" style="display:inline;font">{{collegename}}</div>
        
        
      </el-col>
      <!-- showSelect  -->
      <el-col :span="12" :xs="24">
      <el-row  v-if="showSelect" type="flex" justify="end" >
        
        <!-- 0 -->
        <el-button @click="requestNav" v-if="years.length==0"></el-button>
        <!--  -->
        <el-select v-model="year" v-on:change="selectYear">
          <el-option v-for="item in years" :key="item" :label="item + ''" :value="item"></el-option>
        </el-select>

        <!--  -->
        <el-select v-model="term" v-on:change="selectYear">
          <el-option
            v-for="item in terms"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>

        <el-cascader placeholder="" :options="classes" @change="intoClass"></el-cascader>
      </el-row>
      </el-col>

    </el-row>
  </div>
</template>

<script>
import { store } from "../store.js";
import { college } from "../api/testdata";
import { EventBus } from "../event-bus.js";
import { get, post } from "../api/http.js";
import { apiLogin, apiLogoNav } from "../api/api.js";
import { ClassData } from "../api/testclassdata.js";
import { StudentData } from "../api/teststudent";
import { arrayFill } from "../utils/tools.js";
export default {
 name:'app-logo',
 mounted(){
      EventBus.$on("hideLogo",(show)=>{
            console.log("loggo:"+show)
            this.showSelect=show;
        })
        EventBus.$on("requestData",(from)=>{
            console.log(`:${from}`)
            // 
            this.requestNav()
        })
 },
  //
  created(){
   
    console.log("logo.......")
    
    this.$router.afterEach((to,from)=>{
      console.log("after each")
      //
    })
    this.$router.beforeEach((to, from, next) => {
      console.log("Logo.vue beforeEach....." + this.$router.currentRoute.name);

      next();
    });
  },
  computed: {
    // 
    collegename()
    {
      console.log("...")
      console.log(this.$route.params)
      if(Object.keys(this.$route.params).indexOf("collegeid")!=-1)
      {
      return this.$route.params['collegeid'];
      }
      else
      return ""
    }

  },
  beforeRouteEnter(to, from, next) {
    console.log("beforeRouteEnter....");
    console.log(to);
  },
  methods: {
    // ,
    requestNav() {
      // 
      this.loading = true;
      // ,,
      apiLogoNav({
        college:this.collegename
      })
        .then(res => {
          this.loading = false;
          //
          console.log("logo!!");
          console.log(res);
          // 
          arrayFill(this.classes, res.data.classes);
          // 
          arrayFill(this.years, res.data.years);
          console.log("...");
          console.log(this.classes);
          // 0
          this.year = this.years[0];
          // 
          this.selectYear();
        })
        .catch(err => {
          this.loading = false;
          this.showFailDialog("!!", this.requestNav);
          //  
          this.selectYear();
        });
    },
    //
    home() {
      this.$router.push({
        name: "data"
      });
    },
    // 
    intoClass(value, value2) {
      console.log(":" + value);
      // id
      let id = value.join("");
    // 
       this.$route.params["classid"]=id;
      this.$router.push({
        name: "class",
        params: {
          classid: id
        }
      });
      //this.selectYear()
    },
    //
    //   ,
    selectYear() {
      this.showSelect = true;
      console.log(":");
      console.log(this.$router.currentRoute);
      //
      console.log("year:" + this.year + " term:" + this.term);
      store.setYear(this.year);
      store.setTerm(this.term);
      this.loading = true;
      //
        if (this.$router.currentRoute.name == "college") {
          this.requestCollege();
        } else if (this.$router.currentRoute.name == "class") {
          //
          let classid = this.$router.currentRoute.params["classid"];

          this.requestClass(classid);
        } else if (this.$router.currentRoute.name == "student") {
          
          this.requestStudent(
            this.$router.currentRoute.params["studentid"],
            this.$router.currentRoute.params["classid"]
          );
        }
        //
        //
        else {
          this.loading = false;
        }
      },
    // ,,retryCallback
    showFailDialog(message, retryCallback) {
      this.$confirm(message, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        callback: action => {
          console.log("...");
          console.log(action);
          // 
          if (action == "confirm") {
            if (retryCallback != undefined) retryCallback();
          }
        }
      });
    },
    requestCollege() {

      post("/api/collage", {
        college:this.collegename,
        year: store.state.year,
        term: store.state.term
      })
        .then(res => {
          console.log(":..");
          console.log(res);
          this.loading = false;
          // 
          Object.assign(college, res);
          EventBus.$emit("collegeDataLoad", college);
        })
        .catch(err => {
          console.warn(",...>>>");
    
          this.showFailDialog(":\n" + err, this.selectYear);
          //
          //
          this.loading = false;
          // 
          EventBus.$emit("collegeDataLoad", college);
        });
    },
    requestClass(classid) {
      console.log("-------------------------------------");
      post("/api/banji", {
        college:this.collegename,
        classid: classid,
        year: store.state.year,
        term: store.state.term
      })
        .then(res => {
          console.log(":..");
          console.log(res);
          this.loading = false;
          Object.assign(ClassData, res);
          EventBus.$emit("classDataLoad", ClassData);
        })
        .catch(err => {
          console.log(">>>");
          console.log(err);
          //
          //
          this.loading = false;
          EventBus.$emit("classDataLoad", ClassData);
          this.showFailDialog(":\n" + err, this.selectYear);
        });
    },
    requestStudent(studentid, classid) {
      console.log("....");
      console.log(this.$route.params);

            post("/api/geren",{
              college:this.collegename,
              stu_id:studentid,year:store.state.year,term:store.state.term,classid:classid}).then(res=>{
              console.log(":..")
              console.log(res)
              console.log(typeof res)
              
              
               this.loading=false
               //StudentData
               Object.assign(StudentData,res)
              EventBus.$emit("studentDataLoad",StudentData)
            }).catch(err=>{
            this.$alert(err,"",{
            confirmButtonText:""
          })
              console.log(">>>----------")
              console.log(err)
              this.showFailDialog(":\n"+err,this.selectYear)
               this.loading=false
              //
             EventBus.$emit("studentDataLoad",StudentData)
            })
        }
    
  },
  data() {
    return {
      //
      showSelect: false,
      loading: false,
      year: store.state.year,
      term: store.state.term,
      // 
      classes: [
        {
          label: "17",
          value: "17",
          children: [
            { label: "", value: "bigdata" },
            { label: "", value: "clound" }
          ]
        },
        {
          label: "18",
          value: "18",
          children: [
            { label: "", value: "bigdata" },
            { label: "", value: "clound" }
          ]
        }
      ],

      years: [],
      terms: [
        {
          value: "term1",
          label: ""
        },
        {
          value: "term2",
          label: ""
        },
        {
          value: "year",
          label: ""
        }
      ]
    };
  }
};
</script>

