// 
export const store = {
    //    
    
    
    state:{
        // 
        login:{
            // 
            username:""
        },
        //
        year:"2019",
        //
        term:"term1",
        // 
        
        colleges:["","","","","","","","","","","","","","","      ","",""]
        ,
        //
        termName(){
            if(this.term=="term1")
            {
                return ""
            }
            else if(this.term=="term2")
            {
                return ""
            }
            else if(this.term=="year"){
                return ""
            }
        }

    },
    setUserName(username){
        this.state.login.username=username;
    },
    setStudent(stu){
        this.state.nowStudent=stu
    },
    setYear(year){
        this.state.year=year
    },
    setTerm(term){
        this.state.term=term
    }

}