<template>
<div>
  <!--  -->
    <el-row :gutter="40">
    <!--  -->
    <!--  -->
      <el-col :span="4" :xs="24"><el-card >
        <div slot="header" class="clearfix">
            <span></span>
          </div>
          <div>{{data.classjbCard.classname}}</div>
          <div>:{{data.classjbCard.students}}</div>
      </el-card>
      <!--  -->
      <el-card>
        <el-collapse v-model="activeNames" @ change="handleChange">
     <el-collapse-item title="" name="1">
       <div>{{proposal}}</div>
       <div>{{proposal2}}</div>
      
     </el-collapse-item>
   </el-collapse>
      </el-card>
      </el-col>
   <!--   -->
   
      <el-col :span="11"  :xs="24">
        <el-card>
        <v-chart class="chart" ref="suchindexscore"  autoresize></v-chart>
        <!-- ref -->
        </el-card>
      </el-col>
   
    <!--  -->
    
      <el-col :span="9" :xs="24" >
        <el-card >
        <el-table  @row-click="intoStudent" :data="peoples" height="400" style="width: 100%">
          <!--  @row-click,peoples -->
          
          <el-table-column prop="name" label=""></el-table-column>
          
        </el-table>
        </el-card>
        </el-col>
    
  </el-row>

  <!--  -->
   <el-row :gutter="40"  style="margin-top: 25px;" >
     <!--  -->
     <el-col :span="4" :xs="20">
       <el-card>
         <div slot="header" class="clearfix">
           <span></span>
         </div>
           <div>:{{data.classCard1.term1_score}}</div>
        <div>:{{data.classCard1.term1_paiming}}</div>
       </el-card>
       <el-card>
         <div slot="header" class="clearfix">
           <span></span>
         </div>
        <div>:{{data.classCard2.term2_score}}</div>
        <div>:{{data.classCard2.term2_paiming}}</div>
       </el-card>
     </el-col>
     <!--  -->
     <!-- top  -->
     
     <el-col :span="11" :xs="24" >
         <el-card>
       <el-select v-on:change="zhibiaochange" v-model="nowIndex"  clearable placeholder="" style="margin-left: 300px;" size="small"> 
<!-- v-on:change v-model -->
       <el-option
       v-for="item in options"
       :key="item.value"
       :label="item.label"
       :value="item.value"></el-option>
       </el-select>
       
       <!-- top5 -->
        <v-chart class="chart" ref="topstudent" autoresize ></v-chart>
        <!-- ref -->
  </el-card>
       </el-col>
   
       <!--  -->
      <el-col :span="9" :xs="24">
        <el-card body-style="padding:36px" >
        <v-chart class="chart" ref="totalscores"   autoresize >
          <!-- ref -->
        </v-chart>
        </el-card>
      </el-col>
     
   </el-row>
</div>
</template>


<script>
 import {ClassData} from '../api/testclassdata.js'
    import {store} from '../store.js'
    import {EventBus} from '../event-bus.js'
export default {
  name:'banji',
  beforeRouteEnter(to,from,next){
    console.log("before router enter")
    next()
  },
  beforeRouteUpdate(){
    console.log("..")
    EventBus.$emit("requestData","")
  },
  mounted(){
     let indexs=['','','','','','','']//
      console.log("Class mounted")  
        EventBus.$on("classDataLoad",data=>{
          //
          this.hideLoad()
      console.log("...")
      console.log(data)
      this.data=data
      //
      this.set_topstudent()
      this.set_suchindexscore()
      this.set_studentnames()
      this.set_topstudent()
      this.set_totalscores()
      //
      let minIndex=0
      let temp=this.data.suchindexscores[1].value;
      console.log("...")
      console.log(temp)
      for(let i=0;i<temp.length-2;i+=1)
      {
          if(temp[i]<temp[minIndex])minIndex=i
      }

      this.proposal2=""+indexs[minIndex]+","

      let score=0
    
      if(store.state.term==="term1")
      score=this.data.classCard1.term1_score;
      else
      score=this.data.classCard2.term2_score;
      console.log(store.state.term+"score:"+score)
      console.log(this.data.classCard1.term1_score)
      if (score>=0&&score<=15){
        this.proposal=""
      }
      else if (score>15&&score<=30){
        this.proposal=""
      }
      else if (score>30&&score<=50){
        this.proposal=""
      }

    })
    this.showLoad()
    //
    EventBus.$emit("requestData","")
  
  },
  methods:{
    showLoad(){
       Object.keys( this.$refs).forEach(key => {
        this.$refs[key].showLoading()
      })},
       hideLoad(){
        //
       Object.keys( this.$refs).forEach(key => {
        this.$refs[key].hideLoading()
      })
    },
    //
    intoStudent(row, column, event){
   
      console.log(":"+row+" "+column+" "+event)
      console.log(this.$route);
         this.$router.push({
        name:"student",
        params:{
          studentid:row.name,
          classid:this.$route.params['classid']
        }
      })
    },
    //
    zhibiaochange(value){
      console.log("")
      console.log(value)
      this.set_topstudent()
      

    },
      handleChange(val){
      
    },
    //
    set_suchindexscore()
    {
    console.log("")
    //
    let suchindexscores=this.data['suchindexscores']
    let maxvalue = -1;

      
      for(let i =0;i<suchindexscores.length;i+=1)
      {
        for(let j =0;j<suchindexscores[i].value.length;j+=1)
        {

      
          if(suchindexscores[i].value[j]>maxvalue)
          maxvalue=suchindexscores[i].value[j]
        }
      }

   
    let option={
      
          title:{text:`${this.stateStore.termName()} `},
        tooltip:{},
        legend:{data:['',''],right:0},
        radar:{name:{
          textStyle:{
            color:'#000',backgroundColor:'#999',borderRadius:3,padding:[3,5]}},
            indicator:[{name:'',max:maxvalue},
            {name:'',max:maxvalue},
            {name:'',max:maxvalue},
            {name:'',max:maxvalue},
            {name:'',max:maxvalue},
            {name:'',max:maxvalue},
            {name:'',max:maxvalue}
            ]
            },
             
            series:[{
              name:'vs',
            type:'radar',
            data:
suchindexscores
             
              
              }]



    }
   
    this.$refs['suchindexscore'].mergeOptions(option)//
    

    }, 
    //
    //for
    set_studentnames(){
      let student=this.data['students']['student']
      this.peoples.splice(0,this.peoples.length); 
      for(let i =0;i<student.length;i+=1)
      this.peoples.push(
        {
        name:student[i]
        }
        )
      
    },



    // top 
    set_topstudent()
    {
      console.log("top:"+this.nowIndex)
      if( Object.keys(this.data['topstudent']).indexOf(this.nowIndex)<0 )
      {
        console.log("")
        return;
      }
      let names = this.data['topstudent'][this.nowIndex]['names']
      let scores = this.data['topstudent'][this.nowIndex]['scores']
      let option={
        
        title:{text:` ${this.stateStore.termName()} `},
       color:['#00E5EE'],
       tooltip:{trigger:'axis',
       axisPointer: {
         type: 'shadow'
       }
       },
       grid:{
         left:'3%',
         right:'4%',
         bottom:'3%',
         containLabel:true
       },
       xAxis:[{
         type:'category',
         data:names,
         axisTick:{
           alignWithLabel:true
         }
       }],
       yAxis:[{
         type:'value'
       }],
       series:[{
         name:'',
         type:'bar',
         barWidth:'60',
         data:scores
       }]
     }

     this.$refs['topstudent'].mergeOptions(option)//
    },
    //
    set_totalscores()
    {
      console.log("")
      let ranges=this.data['totalscores']['ranges']
      let allscores=this.data['totalscores']['allscores']
      let option={
         title:{text:`${this.stateStore.termName()}`},
     tooltip:{trigger:'item',
     fromatter: "{a} <br/>{b} : {c}%"},
     toolbox:{
       feature:{dataView:{readOnly: false},
       restore: {},
       saveAsImage:{}
       }
     },
     
     legend:{
       data:ranges,
       bottom:0
     },
     calculable: true,
     series:[
       {
         name: '',
         type:'funnel',
         left:'20%',
         top:60,
         bottom:60,
         width:'80%',
         min:0,
         max:50,
         minSize:'0%',
         maxSize:'100%',
         sort:'descending',
         gap:2,
         label:{
           show:true,
           position:'inside'
         },
         labelLine:{
           length:10,
           lineStyle:{
             width:1,
             type:'solid'
           }
         },
         itemStyle:{
           borderColor:'#fff',
           borderWidth:1
         },
         emphasis:{
           label:{
             fontSize:20
           }
         },
         data:allscores
       }
     ] 


      }
      console.log(this.$refs['totalscores'])
      console.log(option)
      //this.$refs['totalscores'].setOption (option)
  
      this.$refs['totalscores'].mergeOptions(option,false)//
    }
  },

  data:function(){
    return{
        proposal:[],
        proposal2:"",
        stateStore:store.state,
        nowIndex:"Top5",
        data:ClassData,
        peoples:["",""],//
       
      //
      options:[{value:'',label:'Top5'},
               {value:'',label:'Top5'},
               {value:'',label:'Top5'},
               {value:'',label:'Top5'},
               {value:'',label:'Top5'},
               {value:'',label:'Top5'},
               {value:'',label:'Top5'},
               {value:'',label:'Top5'}],
               value:'',
   
    activeNames:[1]//
    }
  }

}
</script>

<style>
 .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  
  
  
</style>