<template>
  <div>

    <!--  -->
    <el-row :gutter="40" >
      <!--  -->
      <el-col :span="5" :xs="24" >
          <!-- cardtext -->
            <el-card body-style="padding:26px">
          <div slot="header">
            <span>{{this.stateStore.year}}</span>
          </div>
          <div class="cardtext">{{data.basicCard.year_score}}</div>
        </el-card >
        <el-card body-style="padding:26px">
          <div slot="header">
            <span>{{this.stateStore.year}}</span>
          </div>
          <div class="cardtext">{{data.basicCard.term1_score}}</div>
        </el-card>
          <el-card body-style="padding:26px">
          <div slot="header">
            <span>{{this.stateStore.year}}</span>
          </div>
          <div class="cardtext">{{data.basicCard.term2_score}}</div>
        </el-card>
  
        
        
        <!-- 20px  -->
        <!-- <el-card style="margin-top: 20px">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <div>1</div>
          <div> 2 </div>
        </el-card> -->
      </el-col>
        <!--  -->
        
      <el-col :span="8" :xs="24">
        <el-card>
          <!-- tooltip  -->
           <el-tooltip class="item" effect="dark" content="" placement="bottom">
      <v-chart  class="chart" autoresize  ref="index" v-on:pieselectchanged="indexChange"></v-chart>
    </el-tooltip>
        </el-card>
      </el-col>
      <!-- 1.2 top5  -->
      <el-col :span="11" :xs="24" >
         <el-card>
        <v-chart  @click="intoClass" class="chart" ref="classtop" autoresize ></v-chart>
         </el-card>
      </el-col>
  
    </el-row>

    <!--  -->
    <el-row :gutter="40" style="margin-top: 25px;">  
      <!-- top 50  -->
      <el-col :span="5" :xs="24">
         <el-card body-style="padding:12px">
        <div style="text-align:center;font-weight:bold">{{this.stateStore.year}}  {{this.stateStore.termName()}} {{this.nowIndex}} TOP50 </div>
        <el-table :data="studenttop50" height="400">
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column property="name" label=""></el-table-column>
        <el-table-column property="score" label=""></el-table-column>
      </el-table>
       </el-card>
      </el-col>
      
      <!--  -->
      <el-col  :span="8" :xs="24">
         <el-card body-style="padding:30px">
        <v-chart class="chart" ref="range" autoresize ></v-chart>
         </el-card>
      </el-col>
<!--  -->
      <el-col :span="11" :xs="24">
        <el-card body-style="padding:30px">
          <v-chart class="chart"  ref="gpa_score" autoresize > </v-chart>
        </el-card>
        
      </el-col>
    </el-row>



    <!--  -->
    <el-row :gutter="15" style="margin-top: 25px;" >

      <!--  -->
      <el-col :span="13" :xs="24">
        <el-card>
        <v-chart  ref="trend" class="chart" autoresize > </v-chart>
         </el-card>
      </el-col>

    </el-row>


  </div>
</template>


<script>
// 
import {store} from '../store.js'
import {EventBus} from '../event-bus.js'
import {college} from '../api/testdata.js'
export default {
  //vue
  //
  created(){

  
  },
  // 
  mounted(){
    console.log(" mounted...")
    
    // 
    EventBus.$on("collegeDataLoad",data=>{
      console.log("...")
      // 
      this.data=data
      // 
      this.nowIndex=''
      // 
      this.setIndex()
      // top
      this.set_classtop()
      // gpa
      this.set_gpa_score()
      // 
      this.set_range()
      // 
      this.set_trend()
      //
      this.hideLoad()
      
    })

  //
  this.showLoad()
  //Logo,
  EventBus.$emit("requestData","")
  },
  name: "College",
  methods:{
    //
    intoClass(params){
      console.log("..")
    // 
    this.$router.push({
        name:"class",
        params:{
          classid:params.name,
          collegeid:""

        }
      })

    },
    //
    showLoad(){
       Object.keys( this.$refs).forEach(key => {
        this.$refs[key].showLoading()
      })},

    hideLoad(){
        //
       Object.keys( this.$refs).forEach(key => {
        this.$refs[key].hideLoading()
      })
    },
    //
    indexChange(event){
        console.log(":"+event.name)

        //
        let now="";
        //,
        for(let index in event.selected)
        {
          if(event.selected[index]==true)
          {
            now=index;
          }
        }

        console.log(":"+now)
        this.nowIndex=now;
        //top
        this.set_classtop()
    },

    //top
    set_classtop(){
     console.log("top:"+this.nowIndex)
      let chart = this.$refs['classtop'];
      // 
      if(Object.keys(this.data['top']).indexOf(this.nowIndex)==-1)
      {
        console.warn("top,"+this.nowIndex+"")
        return;
      }
      let classes = this.data['top'][this.nowIndex]['classes']
      let names = [];
      let scores = [];
      classes.forEach(element => {
        names.push(element['name'])
        scores.push(element['score'])
        
      });
      // 
      names=names.reverse()
      scores = scores.reverse();

      //
      let option= {
        title: { text: `${this.stateStore.year}  ${this.stateStore.termName()} ${this.nowIndex} TOP5` },
        tooltip:{},
       
        grid:{
          left:'20%'
        },
        xAxis: {
          type:"value",
          name:"",
          boundaryGap :false,
          axisTick:{
            interval :0
          },
          axisLabel:{
            interval:0
          }

        },
        yAxis: {
          name:"",
          type:"category",
          data: names

        },
        series: {
          type: "bar",
          data: scores

        }
      }
      chart.mergeOptions(option);
      
    },
    //
    set_range(){
      let ranges = this.data['range']['ranges']
      let term1_scores = this.data['range']['term1_scores']
      let term2_scores = this.data['range']['term2_scores']
      let option = {
        tooltip:{
          formatter:function (params) {
            let dataIndex= params.dataIndex;
            let color=params.color;
            return `<div style="color:${color}"></div>:`+term1_scores[dataIndex]+`<div style="color:${color}"></div>:`+term2_scores[dataIndex]+`<div style="color:${color}"></div>:`+(term1_scores[dataIndex]+term2_scores[dataIndex])
            
          }
        },
          title:{text:`${this.stateStore.year} `},
          xAxis:{
            data:ranges,
            name:"",
            axisLabel:{
              interval:0
            }
          },
          yAxis:{
            name:"/"
          },
          legend:{
            right:0
          },
          series:[
           { 
             stack:"",

             name:"",
             type:"bar",
            data:term1_scores
           },
           { 

             stack:"",
             name:"",
             type:"bar",
            data:term2_scores
           }
          ]
      }

      this.$refs['range'].mergeOptions(option)

    },
  //gpa
    set_gpa_score(){ 
   
      let gpa_scores = []
      for(let i =0 ;i<this.data['gpa_score']['gpas'].length;i+=1)
      {
        gpa_scores.push([
          this.data['gpa_score']['gpas'][i],
          this.data['gpa_score']['scores'][i]
        ])
      }
      let option={
          backgroundColor:"white",
        tooltip:{},
       title: { text:"GPA" },
        xAxis: {
          name:"GPA"
        },
        yAxis: {
          name:""
        },
        series: {
          type: "scatter",
          data: gpa_scores
        }
        }
    this.$refs['gpa_score'].mergeOptions(option)
    },
    // 
    set_trend(){

      let chart = this.$refs['trend']
      let option={
         backgroundColor:"white",
        tooltip:{},
         title:{text:""},
      xAxis:{
        name:"",
        boundaryGap:false,
        data:this.data['trend']['years'],
      },
      yAxis:{
        name:""
      },
      legend:{},
      series:
      [
       {
         stack:"",
         name:"",
         type:'line',
         data:this.data['trend']['term1'],
         areaStyle:{
         }
       },
       {
         stack:"",
         name:"",
         type:'line',
         data:this.data['trend']['term2'],
         areaStyle:{
         }
       }
      ]
      }
      
      chart.mergeOptions(option)
    },
    //
    setIndex()
    {
      console.log("setIndex:")
      let piedata = [];
      for(let i =0;i<this.data.indexes.indexes.length;i+=1)
      {
        // 
        if(this.data.indexes.indexes[i]=="")
        continue
        piedata.push({
          name:this.data.indexes.indexes[i],
          value:this.data.indexes.scores[i]
        })
      }
      let option={ 
        tooltip:{},
  
        title: { text: `${this.stateStore.year}  ${this.stateStore.termName()}  ` },
        series: {
             label:{
          show:true,
           formatter:"{b}({d}%)"
        },
          type: "pie",
          // 
          selectedMode:"single",
          // 						
          data:piedata
        }}
      let chart=this.$refs['index']
      chart.mergeOptions(option)

    }
  },
  computed:{
    // , nowIndex
    studenttop50()
    {
      console.log("Top 50 :now index:"+this.nowIndex)
      if(  Object.keys( this.data['top']).indexOf( this.nowIndex)==-1 )
      {
        console.log(":"+this.nowIndex);
        return;
      }
      // 
       let students = this.data['top'][this.nowIndex]['students']
       return students;
    }
  },
  data() {
    return {
      //
      nowIndex:"",
      stateStore:store.state,
      //,
      data:college

    };
  }
};
</script>

<style scoped>
/*  */
.cardtext {
color:red;font-size:30px;
}
.chart {
width: 100%;
}
</style>